import arrayIdentite from "../../Object/services/supportCommunication.json";
import Helmet from "react-helmet";

export default function SupportCommunication() {
  return (
    <section id="webdesign">
      <Helmet>
        <title>
          Cration de supports de communication - Graphiste Designer print/web 
          Paris
        </title>
        <meta
          name="description"
          content="Je vous accompagne dans la cration de vos supports de communication, de la conception  la ralisation. Affiche, illustration, plaquettes de communication"
        />
      </Helmet>
      <div className="container-main">
        <About
          img={supportCom}
          className="padding-services"
          sizeTitle="55px"
          sizeTxt="30px"
          title="Supports de communication"
          txt="Vos supports de communication print sont le reflet de vos valeurs et de votre qualit de service. Ils doivent valoriser vos produits, attirer lil de vos clients et leur donner envie de vous faire confiance.<br/>

                <p>Lorsque vous me confiez la ralisation de ces supports, je prends le temps de comprendre vos enjeux afin de ne pas crer un nime document qui ne vous permettrait pas datteindre vos objectifs. Mon ambition est de vous faire bnficier doutils valorisants et de qualit pour votre activit.</p>  
                
                <p>Je vous accompagne jusqu la phase dimpression au cours de laquelle je gre pour vous toute la partie oprationnelle. Vous vous dchargez de tous les aspects techniques. Je vous livrer un produit fini, prt  tre diffus.</p>"
        />
      </div>
      <QuestionsCourantes servicesArray={arrayIdentite} />
    </section>
  );
}
